<template>
  <v-card>
    <v-card-text>
      <v-row>
        <v-col cols="6"
               class=d-flex>
          <v-row>
            <v-col cols="12">
              <mrc_ess_datePicker @getTime='paramsChange'
                                  :disabled="disabled"
                                  :label="$t('label_Date_of_birth')"
                                  v-model="stafflist.date_of_brith"></mrc_ess_datePicker>
            </v-col>
            <v-col cols="12">
              <mrc_ess_multiText :label="$t('label_Marital_status')"
                                 :isNational="judegNational('maritual_status')"
                                 v-model="stafflist.maritual_status"
                                 :readonly='disabled'></mrc_ess_multiText>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="6"
               v-if="!disabled">
          <h5 class="left">{{$t('label_Gender')}}</h5>
          <v-radio-group v-model="stafflist.gender"
                         row
                         :mandatory="false">
            <v-radio :label="$t('label_man')"
                     color="#ff8100"
                     value='1'></v-radio>
            <v-radio :label="$t('label_woman')"
                     color="#ff8100"
                     value='0'></v-radio>
          </v-radio-group>
        </v-col>
        <v-col cols="6"
               v-else>
          <v-text-field :label="$t('label_Gender')"
                        style="padding-top:15px"
                        hide-details="auto"
                        :disabled='disabled'
                        v-model="sex"></v-text-field>
        </v-col>

      </v-row>
      <!-- <v-row v-if="!disabled"
             class="ml-1 mr-1">
        <v-col cols='2'>
          <mrc_ess_prompt :label="$t('label_The_area_code')+'*'"
                          :disabled='disabled'
                          :rules='false'
                          :chips="false"
                          v-model="stafflist.mobile_number_area_code"
                          obj="country_mobile_prefix">
          </mrc_ess_prompt>
        </v-col>
        <v-col cols='4'> -->
      <!-- <v-text-field label="手机电话号码"
                      v-model="stafflist.mobile_number"
                      :error-messages="$errors('stafflist','mobile_number',[{rule:'areaRequired',msg:'区号没填'},{rule:'phoneVerify',msg:'电话号码格式不对,请输入正确的号码格式'}])"
                      hide-details="auto"
                      :readonly='disabled'></v-text-field> -->
      <!-- <v-text-field :label="$t('label_Phone_number')"
                        v-model="stafflist.mobile_number"
                        hide-details="auto"
                        :readonly='disabled'></v-text-field> -->
      <!-- <mrc_ess_multiText label="手机电话号码"
                           :isNational="judegNational('mobile_number')"
                           v-model="stafflist.mobile_number"
                           :readonly='disabled'></mrc_ess_multiText> -->
      <!-- </v-col>
        <v-col cols='2'>
          <mrc_ess_prompt :label="$t('label_The_area_code')+'*'"
                          :disabled='disabled'
                          :rules='false'
                          :chips="false"
                          v-model="stafflist.home_number_area_code"
                          obj="country_mobile_prefix">
          </mrc_ess_prompt>
        </v-col>
        <v-col cols='4'> -->
      <!-- <v-text-field label="住宅电话号码"
                      v-model.number="stafflist.home_number"
                      :error-messages="$errors('stafflist','home_number',[{rule:'areaRequired',msg:'区号没填'},{rule:'phoneVerify',msg:'电话号码格式不对,请输入正确的号码格式'}])"
                      hide-details="auto"
                      :readonly='disabled'></v-text-field> -->
      <!-- <v-text-field :label="$t('label_Home_phone_number')"
                        v-model.number="stafflist.home_number"
                        hide-details="auto"
                        :readonly='disabled'></v-text-field>
        </v-col>
      </v-row>

      <v-row v-else
             class="ml-1 mr-1">
        <v-col cols='6'>
          <v-text-field :label="$t('label_Phone_number')"
                        v-model.number="mobile_number"
                        hide-details="auto"
                        :readonly='disabled'></v-text-field>
        </v-col>
        <v-col cols='6'>
          <v-text-field :label="$t('label_Home_phone_number')"
                        v-model.number="home_number"
                        hide-details="auto"
                        :readonly='disabled'></v-text-field>
        </v-col>
      </v-row> -->
      <v-row>
        <v-col cols="6">
          <mrc_ess_phoneInput :readonly='disabled'
                              v-model="stafflist.mobile_number"
                              :changeCode.sync="stafflist.mobile_number_area_code"
                              :code="stafflist.mobile_number_area_code">
          </mrc_ess_phoneInput>
        </v-col>
        <v-col cols="6">
          <mrc_ess_phoneInput :readonly='disabled'
                              v-model="stafflist.home_number"
                              :changeCode.sync="stafflist.home_number_area_code"
                              :code="stafflist.home_number_area_code">
          </mrc_ess_phoneInput>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="6">
          <!-- <v-text-field label="个人邮箱*"
                      :error-messages="$errors('stafflist','persona_email_address',emailRule)"
                      :readonly="disabled"
                      v-model="stafflist.persona_email_address">
        </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_Personal_mailbox')+'*'"
                             :isNational="judegNational('persona_email_address')"
                             :error-messages="$errors('stafflist','persona_email_address',emailRule)"
                             :readonly="disabled"
                             v-model="stafflist.persona_email_address">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="6">
          <mrc_ess_input :label="$t('label_Fax_number')+'*'"
                         :error-messages="$errors('stafflist','fax',numericRule)"
                         :readonly="disabled"
                         v-model.number="stafflist.fax">
          </mrc_ess_input>

        </v-col>
      </v-row>

      <v-row>
        <v-col cols="2"
               class="f1">
          {{$t('label_address')}}</v-col>
        <v-col cols="7"></v-col>
        <v-col cols="3">
          <!-- <v-btn rounded
                 color="#e3bec8"
                 dark
                 @click="editItem"
                 v-if="!disabled">+{{$t('label_New_address')}}</v-btn> -->
          <v-btn rounded
                 class="ml-5"
                 color="#ffffff"
                 dark
                 style="color: black;"
                 @click="editItem"><span style="color: #0F46C1;font-size: 30px;">+</span>{{$t('label_New_address')}}</v-btn>
        </v-col>
      </v-row>

      <v-card v-for="(item,index) in mrbase_sys_staff_address"
              :key="index"
              class="bigcard">
        <v-card-title class="addresscard">
          <div>
            <v-btn rounded
                   color="rgb(19, 166, 113)"
                   dark
                   x-small
                   v-if="item.status==1">{{$t('label_Effective')}}</v-btn>
            <v-btn rounded
                   color="#cccccc"
                   dark
                   x-small
                   v-else>{{$t('label_Failure')}}</v-btn>
            <!-- <span class="sps1 c1"
                v-if="item.status==1">有效</span>
          <span class="sps1 c2"
                v-else>失效</span> -->
            <span class="sps2">{{$t('label_address')}}{{index+1}}</span>
          </div>
          <div v-if="!disabled">
            <v-icon class="mr-2"
                    @click="editItem(item)">
              mdi-pencil
            </v-icon>
            <v-icon @click="deleteItem(item)">
              mdi-delete
            </v-icon>
          </div>
        </v-card-title>
        <v-card-text>
          <v-text-field :label="$t('label_address')+'*'"
                        v-model="item.address_line_1"
                        :readonly="true">
          </v-text-field>
          <v-text-field :readonly="true"
                        v-model="item.address_line_2">
          </v-text-field>
          <v-text-field :readonly="true"
                        v-model="item.address_line_3">
          </v-text-field>
        </v-card-text>
      </v-card>
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       @cancel='cancel'
                       :loading='btnIsLoading'
                       v-if='!disabled'></mrc_ess_submitBtn>
    <mrc_ess_myDialog v-model="dialog"
                      :title="$t('label_A_new_level')"
                      @success='save'
                      @cancel='close'>
      <v-card-text>
        <mrc_ess_multiText :label="$t('label_address')+'*'"
                           :error-messages="$errors('editedItem','address_line_1',requiredRule)"
                           :isNational="judegNational('address_line_1')"
                           v-model="editedItem.address_line_1"
                           :readonly="disabled">
        </mrc_ess_multiText>
        <mrc_ess_multiText :readonly="disabled"
                           :error-messages="$errors('editedItem','address_line_2',requiredRule)"
                           :isNational="judegNational('address_line_2')"
                           v-model="editedItem.address_line_2">
        </mrc_ess_multiText>
        <mrc_ess_multiText :readonly="disabled"
                           :error-messages="$errors('editedItem','address_line_3',requiredRule)"
                           :isNational="judegNational('address_line_3')"
                           v-model="editedItem.address_line_3">
        </mrc_ess_multiText>
        <mrc_ess_datePicker @getTime='paramsChange2'
                            :label="$t('label_Effective_date')+'*'"
                            :disabled='false'
                            :errMsg="$errors('editedItem','effective_start_date',requiredRule)"
                            v-model="editedItem.effective_start_date"></mrc_ess_datePicker>
      </v-card-text>
    </mrc_ess_myDialog>
  </v-card>
</template>

<script>
import { required, maxLength, between, email, url, numeric } from 'vuelidate/lib/validators'
import { getStaffPersonal, getStaffPersonalUpdate } from '@/service/api/staffmsg.js'
import moment from 'moment'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      btnIsLoading: false,
      dialog: false,
      uuid: '',//唯一标识
      staff_uuid: '',  //关联staff表uuid
      stafflist: {},
      mrbase_sys_staff_address: [    //地址信息

      ],
      editedIndex: -1,
      editedItem: {
        address_line_1: "",    //地址1
        address_line_2: "",    //地址2
        address_line_3: "",    //地址3
        effective_start_date: '',    //地址有效日期
      },
      defaultItem: {
        address_line_1: "",    //地址1
        address_line_2: "",    //地址2
        address_line_3: "",    //地址3
        effective_start_date: '',    //地址有效日期
      },
      deleteAddresss: []  //删除的地址 
    };
  },
  created () { },
  async mounted () {
    await this.national('mrbase_sys_staff_personal_info')
    await this.getlist()
  },
  methods: {
    success () { //提交  moment(time).format('YYYY-MM-DD')
      this.$v.stafflist.$touch()
      if (this.$v.stafflist.$error) return
      this.btnIsLoading = true
      const user_id = this.$store.state.detailuuid
      const uuid = this.$store.state.uuid
      this.stafflist.gender = Number(this.stafflist.gender)
      // console.log(this.stafflist.gender);
      if (this.stafflist.date_of_brith) this.stafflist.date_of_brith = moment(this.stafflist.date_of_brith).format('YYYY-MM-DD')
      this.mrbase_sys_staff_address.map(item => {
        if (item.effective_start_date) item.effective_start_date = moment(item.effective_start_date).format('YYYY-MM-DD')
      })
      getStaffPersonalUpdate({
        uuid: uuid,
        user_id: user_id,
        mrbase_sys_staff_personal_info: [this.stafflist],
        mrbase_sys_staff_address: [...this.mrbase_sys_staff_address, ...this.deleteAddresss]
      }).then(async res => {
        if (res.code === '200') {
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
          await this.getlist()
        }
        this.btnIsLoading = false
      })
    },
    cancel () {
      this.$myConfirm({
        content: this.$t('message_Confirm_the_cancellation_of_the_edit')
      }).then(res => {
        this.$router.push({
          name: 'staffmanagement'
        })
      }).catch(err => { })
    },
    paramsChange () {  //时间改变获取时间
    },
    paramsChange2 () {

    },
    editItem (item) {
      if (item.uuid || item.staff_uuid) {  //传了item就是修改  没传就是添加
        this.editedIndex = this.mrbase_sys_staff_address.indexOf(item)
        this.editedItem = Object.assign({}, item)
      }
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.mrbase_sys_staff_address.indexOf(item)
      this.$myConfirm({
        content: this.$t('message_Confirm_the_deletion')
      }).then(() => {
        const dd = this.mrbase_sys_staff_address.splice(index, 1)
        if (dd[0].uuid || dd[0].staff_uuid) this.deleteAddresss.push({ ...dd[0], "~type~": 'del' })
      })
    },
    async getlist () {
      const uuid = this.$store.state.uuid
      const res = await getStaffPersonal({ staff_uuid: uuid })
      if (Object.keys(res.data).length > 0) {
        this.uuid = res.data.uuid
        this.staff_uuid = res.data.staff_uuid   //后端未返回字段
        this.stafflist = this.$deletefield(null, res.data)
        this.mrbase_sys_staff_address = res.mrbase_sys_staff_address
      } else {

      }
    },
    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      this.$v.editedItem.$touch()
      if (this.$v.editedItem.$error) return
      if (this.editedIndex > -1) {
        Object.assign(this.mrbase_sys_staff_address[this.editedIndex], this.editedItem)
      } else {
        this.mrbase_sys_staff_address.push(this.editedItem)
      }
      this.close()
    },
  },
  computed: {
    mobile_number () {
      if (this.stafflist.mobile_number_area_code && this.stafflist.mobile_number) {
        return '+' + this.stafflist.mobile_number_area_code + '\xa0\xa0\xa0' + this.stafflist.mobile_number
      } else {
        return ''
      }

    },
    home_number () {
      if (this.stafflist.home_number_area_code && this.stafflist.home_number) {
        return '+' + this.stafflist.home_number_area_code + '\xa0\xa0\xa0' + this.stafflist.home_number
      } else {
        return ''
      }
    },
    sex () {
      // console.log(typeof (this.stafflist.gender));
      if (!this.stafflist.gender) return ''
      return this.stafflist.gender == 1 ? this.$t('label_man') : this.$t('label_woman')
    }
  },
  watch: {
  },
  validations: {
    stafflist: {
      date_of_brith: {

      },
      gender: {

      },
      nationality: {

      },
      maritual_status: {

      },
      home_number_area_code: {

      },
      home_number: {
        // areaRequired (value) {
        //   if (value) {
        //     return this.stafflist.home_number_area_code ? true : false
        //   } else {
        //     return true
        //   }
        // },
        // phoneVerify (value) {
        //   if (!value)
        //     return true
        //   const regex = this.$myVerify.phone
        //   return regex.test(this.stafflist.home_number_area_code + value)
        // }
      },
      mobile_number_area_code: {

      },
      mobile_number: {
        // areaRequired (value) {
        //   if (value) {
        //     return this.stafflist.mobile_number_area_code ? true : false
        //   } else {
        //     return true
        //   }
        // },
        // phoneVerify (value) {
        //   if (!value)
        //     return true
        //   const regex = this.$myVerify.phone
        //   return regex.test(this.stafflist.mobile_number_area_code + value)
        // }
      },
      fax: {
        numeric
      },
      corporate_email_address: {
        email
      },
      persona_email_address: {
        email
      },
    },
    editedItem: {    //地址信息
      address_line_1: {
        required
      },
      address_line_2: {
        required
      },
      address_line_3: {
        required
      },
      effective_start_date: {
        required
      },
    }
    ,
  }
};
</script>

<style scoped>
.f1 {
  font-size: 20px;
  font-weight: 550;
}
.bigcard {
  margin-bottom: 20px;
}
.left {
  text-align: left;
}
.addresscard {
  display: flex;
  align-content: center;
  justify-content: space-between;
}
.c1 {
  background: lightgreen;
}
.c2 {
  background: #cccccc;
}
.sps1 {
  height: 25px;
  line-height: 25px;
  border-radius: 12.5px;
  padding: 0 5px;
  display: inline-block;
  font-size: 13px;
  color: white;
}
.sps2 {
  color: #cccccc;
  font-size: 13px;
  padding: 0 10px;
}
</style>
  